
document.addEventListener('DOMContentLoaded',function(){
    menuResponsive();
    darkmode();
    formulario();

});
function darkmode(){
    const preferenciaTemaOscuro=window.matchMedia('(prefers-color-scheme:dark)');
    const btnDarkMode=document.querySelector(".boton-dark-mode");

    // console.log(preferenciaTemaOscuro);
    if(preferenciaTemaOscuro.matches){  
        document.body.classList.add("dark-mode");
        btnDarkMode.src="/build/img/sun-fill.svg";
    }else{
        document.body.classList.remove("dark-mode");
        btnDarkMode.src="/build/img/dark-mode.svg";
    }
    preferenciaTemaOscuro.addEventListener("change",function(){
        if(preferenciaTemaOscuro.matches){  
            document.body.classList.add("dark-mode");
        }else{
            document.body.classList.remove("dark-mode");
        }
    });
    

    btnDarkMode.addEventListener('click',function(){

        document.body.classList.toggle('dark-mode');

        //almacena el modo claro o oscuro en el local storage
        if(document.body.classList.contains('dark-mode')){
            localStorage.setItem('modo-oscuro','true');
            btnDarkMode.src="/build/img/sun-fill.svg";
        }else{
            localStorage.setItem('modo-oscuro','false');
            btnDarkMode.src="/build/img/dark-mode.svg";
        }
    });

    
    if(localStorage.getItem('modo-oscuro')==='true'){
        document.body.classList.add("dark-mode");
        btnDarkMode.src="/build/img/sun-fill.svg";
    }
    else{
        document.body.classList.remove("dark-mode");
        btnDarkMode.src="/build/img/dark-mode.svg";
    }
    console.log(localStorage.getItem('modo-oscuro'));
}

function menuResponsive(){
    const barraMenu =document.querySelector(".menu-mobil");
    barraMenu.addEventListener('click',navegacionResponsive);
}
function navegacionResponsive(){
    const navegacion=document.querySelector('.navegacion');
    navegacion.classList.toggle('mostrar');
    // if(navegacion.style.display=='none'){
    //     navegacion.style.display='block';
    // }else{
    //     navegacion.style.display='none';
    // }
}
function formulario(){
    const inputsMetodo=document.querySelectorAll('input[name="contacto[contacto]"]');
    inputsMetodo.forEach(input=> input.addEventListener('click',mostrarDivSeleccion))
}
function mostrarDivSeleccion(evento){
    const divSeleccion=document.querySelector('#contactoSeleccion');
    //con el evento accedemos al valor del input navegando por el target hasta el valor
    //se evalua que valor se selecciono en los inputs para inyectar el hatml en el div vacio de selecion 
    if(evento.target.value ==='telefono'){
        
        divSeleccion.innerHTML=`
            <p> Elija la fecha y hora para ser contactado</p>
            
            <label for="telefono">Telefono</label>
            <input type="tel" placeholder="Tu telefono" id="telefono" name="contacto[telefono]">

            <label for="fecha">Fecha:</label>
            <input type="date"  id="fecha" name="contacto[fecha]">

            <label for="hora" >Hora:</label>
            <input type="time"  id="hora" min="09:00" max="18:00" name="contacto[hora]">
        `;

    }else if(evento.target.value ==='email'){

        divSeleccion.innerHTML=`
            <label for="email">Email</label>
            <input type="email" placeholder="Tu email" id="email" name="contacto[email]" required>
        `;
    }
}
